import { set } from "lodash";
import jwt from 'jwt-decode'
import React, { useContext, useEffect, useState } from "react";
import { loginUserAPI } from "../api";
import { DataContext } from "../context/DataContext";
import Register from "./Register";

const Login = ({setSeconds, setRefreshToken}) => {
  const [userEmail, setUserEmail] = useState("");
  const [password, setUserPass] = useState("");
  const [errMsg, setErrMsg] = useState("");
  const [needToRegister, setNeedToRegister] = useState(false);
  const { setAccessToken, setTimelimit, setUserID, setEmail, setPassword } = useContext(DataContext);

  useEffect(() => {
    setErrMsg("");
  }, [userEmail, password]);

  useEffect(() => {
    setSeconds(0)
  }, [])

  const handleSubmit = async (e) => {
    e.preventDefault();
    try {
      const { accessToken, refreshToken, timeLimit } = await loginUserAPI(userEmail, password);
      setEmail(userEmail)
      setPassword(password)
      const userID = jwt(accessToken).fetchedData.userID
      setUserID(userID)
      localStorage.setItem("refreshToken", refreshToken);
      localStorage.setItem("timeLimit", timeLimit)
      setRefreshToken(refreshToken)
      setTimelimit(timeLimit)
      setAccessToken(accessToken)
    } catch (e) {
      console.log("error in handleSubmit: ", e);
      setErrMsg(e);
    }
  };

  const register = () => {
    setNeedToRegister(true);
  };

  return needToRegister ? (
    <Register setNeedToRegister={setNeedToRegister} />
  ) : (
    <div className="login-part">
      <h1>    </h1>
      <form onSubmit={handleSubmit}>
        <div className="user-details">
          <label> </label>
          <input
            type="text"
            id="userEmail"
            onChange={(e) => setUserEmail(e.target.value)}
          />
          <label></label>
          <input
            type="password"
            id="password"
            onChange={(e) => setUserPass(e.target.value)}
          />
        </div>
        <div>
          <button className="login-button" type="submit">
            
          </button>
        </div>
        <p> ?</p>
        <button className="createInvoice-button" onClick={() => register()}>
          
        </button>
      </form>
      {errMsg ? <p>     ,    </p> : null}
    </div>
  );
};

export default Login;
